/* General Styling */
body {
    font-family: 'Work Sans', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none; /* Prevent selection */
    background: linear-gradient(135deg, #a8edea, #fed6e3);
  }
  
  /* Container */
  .about-container {
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: center;
  }
  
  /* Header */
  .about-header {
    color: black;
    margin-bottom: 20px;
  }
  
  .about-header h1 {
    font-size: 2rem;
    font-weight: bold;
    text-decoration: underline;
    margin-top: 20px;
    color: #000;
  }
  
  /* Content Section */
  .about-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px;
    margin-bottom: 30px;
  }
  
  .about-left {
    flex: 1;
  }
  
  .about-image {
    width: 100%;
    max-width: 600px;
    height: auto;
    margin: 0 auto;
    border-radius: 12px;
  }
  
  /* Right Section */
  .about-right {
    flex: 1;
    text-align: left;
    padding: 0 50px;
  }
  
  .about-right h2 {
    font-size: 36px;
    font-weight: bold;
    color: #000;
    margin-bottom: 20px;
  }
  
  .about-right p {
    font-size: 18px;
    margin: 15px 0;
    text-align: justify;
    line-height: 1.8;
    color: #555;
  }
  
  /* Features Section */
  .about-features {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center; /* center for balanced layout */
  margin-top: 30px;
}

.feature-card {
  padding: 20px;
  border: none;
  border-radius: 12px;
  background-color: #ffffff;
  font-size: 18px;
  font-weight: 600;
  color: #222;
  text-align: center;
  flex: 1;
  min-width: 200px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

/* Emoji styling */
.feature-card span.emoji {
  font-size: 28px;
  display: block;
  margin-bottom: 10px;
}

/* Hover effect */
.feature-card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 16px rgba(0, 0, 0, 0.12);
}

