/* Popular Courses Section */
body {
  user-select: none; /* Prevent selection */
  background: linear-gradient(135deg, #a8edea, #fed6e3);
}

.courses-page h1 {
    font-size: 2rem;
    font-weight: bold;
    text-decoration: underline;
    margin-top: 20px;
  }
  
 .learner-courses-section {
      padding: 50px 20px;
      text-align: center;
    }
    
   .learner-courses-section h2 {
      text-align: center;
      font-size: 2rem;
      margin-bottom: 30px;
      text-decoration: underline;
    }
  
    /* Sort By Section */
  .courses-page-sort-by {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 20px;
      gap: 10px;
    }
    
    .courses-page-sort-by span {
      font-size: 1.2em;
      font-weight: bold;
      color: #333;
    }
    
    .courses-page-sort-by select {
      padding: 10px 20px;
      background-color: #fff;
      border: 1px solid #ddd;
      border-radius: 5px;
      font-size: 1rem;
      cursor: pointer;
    }
    
    .courses-page-sort-by button {
      background-color: #0505fc;
      color: white;
      padding: 10px 20px;
      font-size: 1rem;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s;
    }
    
    .courses-page-sort-by button:hover {
      background-color: #0000cc;
    }
    
    .courses-page-sort-by button:focus {
      outline: none;
    }
    
    /* Learner Courses Cards Section */
.learner-courses-container-cards {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  flex-wrap: wrap;
  align-items: stretch; /* Ensures equal height */
}

.learner-courses-card-link {
  text-decoration: none;
  color: inherit;
  width: 300px;
  display: flex;
}

.learner-courses-card {
  background: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 0;
  width: 100%;
  text-align: center;
  transition: transform 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 350px;
  box-sizing: border-box;
  margin-top: 5px;
  cursor: pointer;
}

.learner-courses-card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.1);
}

.learner-courses-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  margin-bottom: 10px;
  border-radius: 5px 5px 0 0;
}

.learner-courses-card h3 {
  font-size: 1.2em;
  margin: 10px 0;
}

.learner-courses-card p {
  margin: 5px 0;
  color: #555;
  margin-bottom: 10px;
}

.learner-courses-card p strong {
  font-weight: bold;
  color: #333;
}

.learner-courses-card .learner-btn-read-more {
  background-color: #0505fc;
  padding: 10px 20px;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1em;
  margin: 10px auto 20px;
  display: inline-block;
}

.learner-courses-card .learner-btn-read-more:hover {
  background-color: #0000cc;
}

.learner-courses-card .learner-btn-continue {
  background-color: #0dbc21;
  padding: 10px 20px;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1em;
  margin: 10px auto 20px;
  display: inline-block;
}

.learner-courses-card .learner-btn-continue:hover {
  background-color: #0a7b16;
}


/* Skeleton Loader Styles */
.skeleton {
  background: linear-gradient(90deg, #ececec 25%, #f5f5f5 50%, #ececec 75%);
  background-size: 200% 100%;
  animation: skeleton-loading 1.2s infinite linear;
  border-radius: 6px;
}

@keyframes skeleton-loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

.skeleton-course-img {
  width: 100%;
  height: 200px;
  margin-bottom: 10px;
  border-radius: 5px 5px 0 0;
}

.skeleton-course-title {
  width: 70%;
  height: 22px;
  margin: 16px auto 10px auto;
}

.skeleton-course-meta {
  width: 80%;
  height: 16px;
  margin: 8px auto;
}

.skeleton-course-btn {
  width: 60%;
  height: 36px;
  margin: 18px auto 20px auto;
  border-radius: 5px;
}